import { useState } from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Form, FormGroup, Label, Input } from "reactstrap";
import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';


const ClassModal = (props) => {

    const [assignmentState, setAssignmentState] = useState(false);

    return (
        <Modal size="xl" isOpen={props.modal} toggle={props.toggle}>  
            <ModalHeader>강의 수정하기</ModalHeader>
            <ModalBody>
                <Form>
                    <FormGroup>
                        <Label for="title">제목</Label>
                        <Input
                            id="title"
                            name="title"
                            placeholder="제목을 입력해주세요."
                            type="text"
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label for="content">내용</Label>
                        <CKEditor
                            editor={ ClassicEditor }
                            id="content"
                            name="content"
                            height="200px"
                        />
                    </FormGroup>
                    <FormGroup switch>
                        <Label for="assigment" switch >과제 제출</Label>
                        <Input
                            id="assigment"
                            name="assigment"
                            type="switch"
                            checked={assignmentState}
                            onClick={() => {
                                setAssignmentState(!assignmentState);
                            }}
                        />
                    </FormGroup>
                </Form>
            </ModalBody>
            <ModalFooter>
                <Button color="primary" onClick={ props.toggle }>저장하기</Button>{''}
                <Button color="outline-danger" onClick={ props.toggle }>취소</Button>
            </ModalFooter>
        </Modal>
    );
};

export default ClassModal;
